import { H5OpenApp, isWeixinBrowser } from '@/utils/openApp'
// 
import waves from '@/directive/waves'
export default {
  props: {
    isVideo: {
      type: Boolean,
      default: false
    },
    openPage: {
      type: String,
      default: ''
    },
    contId: {
      type: String,
      default: ''
    },
    luckyId: {
      type: String,
      default: ''
    },
    eventId: {
      type: String,
      default: ''
    },
    uid: {
      type: String,
      default: ''
    },
    player: {
      type: Object
    }
  },
  methods: {
    openIt () {
      if (isWeixinBrowser()) {
        this.$emit('showMask', true)
      } else {
        let adrPath = ''
        let iosPath = ''
        if (this.openPage == 'detail') {
          var _href = window.location.href
          var reg = /\?from=[^&]*&isappinstalled=[0|1]/
          var href = _href.replace(reg, '')
          href = href.replace('sharedetail', 'detail')
          if (this.isVideo) {
            iosPath = '?jt=5&contId=' + this.contId
            adrPath = '?jt=5&contId=' + this.contId + '&content=' + JSON.stringify(this.player)
          } else {
            adrPath = '?jt=1&contId=' + this.contId + '&detailUrl=' + href.split('#')[0].toString() // #/detail.html, #/detail
            iosPath = '?jt=1&contId=' + this.contId + '&detailUrl=' + href.split('#')[0].toString() // ios#/detail.html, #/detailios
          }
        } else if (this.openPage == 'personalPage') {
          adrPath = '?jt=2&uid=' + this.uid
          iosPath = '?jt=2&uid=' + this.uid
        } else if (this.openPage == 'lottery') {
          adrPath = '?jt=4&luckyId=' + this.luckyId
          iosPath = '?jt=4&luckyId=' + this.luckyId
        } else if (this.openPage == 'event') {
          adrPath = '?jt=3&eventId=' + this.eventId
          iosPath = '?jt=3&eventId=' + this.eventId
        } else if (this.openPage == 'topic') {
          adrPath = '?jt=7&eventId=' + this.eventId
          iosPath = '?jt=7&eventId=' + this.eventId
        }
        // '?jt=1&contId=1085465558880620544&detailUrl=http://dev.cogo.club/api/gmh5/index.html#/detail'
        H5OpenApp({iosPath: iosPath, adrPath: adrPath})
      }
    }
  },
  directives: {
    waves
  }
}



