import { isAndroid, isIOS } from '@/utils/platform'
export default {
  name: 'pageHeader',
  props: {
    title: {
      type: String,
      default: ''
    },
    tip: {
      type: String,
      default: ''
    },
    fromWay: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      offsetMark: 0,
      movedToTop: false
    }
  },
  methods: {
    handleClick (evt) {
      this.$emit('on-click', evt)
    },
    goBack () {
      if (this.$route.query.from == 'h5') {
        this.$router.go(-1)
        return
      }
      if (this.$route.query.from !== 'h5') {
        if (isAndroid()) {
          window.glamor.closePage()
          return
        }
        if (isIOS()) {
          this.$device.JsBridge.callHandler('h5LeftBack', {}, (data) => {})
        }
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', () => {
      this.movedToTop = (window.pageYOffset >= 10)
    })
  }
  // mounted () {
  //   this.$nextTick(() => {
  //     window.addEventListener('scroll', () => {
  //       console.log('window.screenTop', document.body.scrollTop)
  //       this.offsetMark = document.body.scrollTop
  //       this.movedToTop = (this.offsetMark >= 10)
  //     })
  //   })
  // }
}



