import imgShowCenter from '../imgShowCenter/index'
export default {
  name: 'avatar',
  components: {
    imgShowCenter
  },
  props: {
    src: {
      type: String,
      default: ''
    },
    // 
    isAuth: [Number, Boolean],
    width: {
      type: Number,
      default: 40
    }
  },
  data () {
    return {
      wide: false,
      tall: false
    }
  },
  mounted () {
    this.checkImg()
  },
  computed: {
    authImgWidth: function () {
      return this.width * 18 / 40 / 37.5 + 'rem'
    },
    // 
    avaterWidth: function () {
      if (this.width >= 40) {
        return this.width / 37.5 + 'rem'
      } else {
        return this.width + 'px'
      }
    }
  },
  methods: {
    checkImg () {
      if (!this.src) return
      const localIng = new Image()
      localIng.src = this.src
      localIng.onload = () => {
        const height = localIng.height
        const width = localIng.width
        this.wide = width > height || false
        this.tall = !this.wide
      }
    }
  },
  watch: {
    src (val) {
      if (val) {
        this.checkImg()
      }
      return val
    }
  }
}



