export default {
  name: 'imgShowCenter',
  props: {
    src: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      baseWidth: 37.5,
      isLongPic: 0
    }
  },
  methods: {
    checkImgDim () {
      if (!this.src) return
      const localIng = new Image()
      localIng.src = this.src
      localIng.onload = () => {
        const height = localIng.height
        const width = localIng.width
        this.isLongPic = width < height
      }
    }
  },
  beforeMount () {
    this.checkImgDim()
  }
}



