import { isIOS } from '@/utils/platform'
export default {
  data () {
    return {
      text: ''
    }
  },
  mounted () {
    if (isIOS()) {
      this.text = 'safari'
    }
  },
  methods: {
    closeMask () {
      this.$emit('closeMask', false)
    }
  }
}



