import avatar from '@/components/AVATAR/'
// 
import waves from '@/directive/waves'
export default {
  name: 'UserInfo',
  components: {
    avatar
  },
  directives: {
    waves
  },
  props: {
    userinfo: {
      type: Object,
      default: () => {}
    },
    isBorder: {
      type: Boolean,
      default: true
    }
  },
  mounted () {},
  data () {
    return {}
  },
  methods: {
    // 
    clickAvatar () {
      this.$emit('clickAvatar', this.userinfo.uid)
    },
    // 
    clickCared () {
      this.$emit('clickCared')
    }
  }
}



