import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css'
export default {
  props: {
    isShow: {
      type: Boolean,
      default: true
    }
  },
  mounted () {
    NProgress.configure({ showSpinner: false, trickleRate: 0.02 })
    NProgress.start()
  },
  destroyed () {
    NProgress.done()
  }
}



